import Button from "components/Button";
import PageTitle from "components/PageTitle";
import { useState } from "react";
import Select from "components/Select";
import Configuration from "./Configuration";
import Templates from "./Templates";
import classNames from "classnames";

export interface EmailsProps {}

const Emails: React.FC<EmailsProps> = () => {
  const [page, setPage] = useState("configuration");
  const pages = [
    { title: "configuration", component: Configuration },
    { title: "templates", component: Templates },
  ];

  return (
    <div>
      <div className="flex justify-between flex-wrap mb-7">
        <PageTitle className="mb-4 mr-4">Emails</PageTitle>
        <Button className="bg-orange-300 hover:bg-orange-301 mb-4 w-140px" noMinWidth>
          Save
        </Button>
      </div>
      <div className="flex flex-wrap lg:flex-nowrap">
        <div className="max-w-192px mb-3 mr-4 block lg:hidden w-full">
          <Select
            onChange={(e) => setPage(e.target.value)}
            value={page}
            options={pages.map((x) => ({ value: x.title, label: x.title }))}
          ></Select>
        </div>

        <div className="min-w-192px flex-shrink-0 mr-4 hidden lg:block select-none">
          {pages.map((x) => (
            <div
              onClick={() => setPage(x.title)}
              key={x.title}
              className={`mb-1 capitalize cursor-pointer ${
                page === x.title ? "bg-orange-300" : "hover:bg-gray-300"
              } rounded h-34px leading-34px px-3`}
            >
              {x.title}
            </div>
          ))}
        </div>
        <div className="lg:flex-grow w-full rounded bg-gray-300 p-7">
          <div className={classNames({ hidden: page !== "configuration" })}>
            <Configuration></Configuration>
          </div>
          <div className={classNames({ hidden: page !== "templates" })}>
            <Templates></Templates>
          </div>
        </div>
      </div>
    </div>
  );
};

export default Emails;
