import Modal from "@material-ui/core/Modal";
import EmailEditor from "react-email-editor";
import Button from "components/Button";
import Input from "components/GrayInput";

export interface EmailEditorModalProps {
  open: boolean;
  onClose: () => void;
}

const EmailEditorModal: React.FC<EmailEditorModalProps> = ({ open, onClose }) => {
  return (
    <Modal open={open}>
      <div className="fixed left-0 top-0 w-full h-full overflow-auto flex p-12">
        <div className="m-auto bg-white">
          <div className="flex justify-between p-4 items-center">
            <div>
              <Button className="bg-orange-300 hover:bg-orange-301 mr-2">Save</Button>
              <Button className="bg-blue-300 hover:bg-blue-400 text-white">Download</Button>
            </div>
            <Button className="bg-blue-300 hover:bg-blue-400 text-white" onClick={onClose}>
              Close
            </Button>
          </div>
          <EmailEditor></EmailEditor>
        </div>
      </div>
    </Modal>
  );
};

export default EmailEditorModal;
