import { useEffect, useState } from "react";
import Button from "components/Button";
import EmailEditorModal from "components/EmailEditorModal";

export interface TemplatesProps {}

const Templates: React.FC<TemplatesProps> = () => {
  const [openEditor, setOpenEditor] = useState(false);
  return (
    <div>
      <div className="flex justify-between mb-3">
        <div className="font-medium text-22px mb-4 mr-3">Templates</div>
        <Button
          className="mb-4 bg-blue-300 hover:bg-blue-400 text-white"
          onClick={() => setOpenEditor(true)}
        >
          Create
        </Button>
      </div>
      <EmailEditorModal open={openEditor} onClose={() => setOpenEditor(false)}></EmailEditorModal>
    </div>
  );
};

export default Templates;
